import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

  const routes = [
  {
    path:'/',
    name:'首页',
    component: () => import('../views/upload.vue')
  },
  {
    path:'/upload',
    name:'上传',
    component: () => import('../views/upload.vue')
  },
  {
    path:'/edit',
    name:'编辑',
    component: () => import('../views/edit.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
