<template>
    <div class="mynav">
        <div class="appear" v-on:click='appear'></div>
        <div class="console">blog - console</div>
    </div>
</template>
<script>
export default {
    name:'mynav',
    methods: {
        appear() {
            if(document.getElementsByClassName('left')[0].style.left == '0px'){
               document.getElementsByClassName('left')[0].style.left = '-120px'
            }else{
                document.getElementsByClassName('left')[0].style.left = '0px'
            }
        }
    },
}
</script>
<style lang="css">
    .mynav{
        display: flex;
        justify-content: center;
        align-items: center;
        text-align: center;
        position: relative;
        width: 100vw;
        min-width: 1200px;
        background: rgb(66, 31, 131);
        margin: 0;
        padding: 20px 0;
    }
    .appear{
        position: absolute;
        left: 30px;
        margin: 10px;
        color: #fff;
        font-size: 40px;
        width: 40px;
        height: 40px;
        font-weight: bold;
        box-shadow: 0 0 10px rgb(16, 2, 36);
        border-radius: 40px;
    }
    .console{
        font-size: 32px;
        color: rgb(255, 255, 255);
    }
</style>