import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  // 通过 this.$store.以下.名称 访问
  state: {
    // 状态，相当于vue的data
    home:'这是home',
    editcontent:'',
    edittit:'',
    editdate:'',
    istrue:false,
    isupdata:true
  },
  getters:{
    // 计算属性，相当于vue的compute
  },
  mutations: {
    changedata(state,change){
      state.editcontent = change
    },
    changetit(state,change){
      state.edittit = change
    }, 
    changedate(state,time){
      state.editdate = time
    },
    istrue(state,boolean){
      state.istrue = boolean
    },
    isupdata(state){
      state.isupdata = !state.isupdata
    }
  },
  actions: {
  },
  modules: {
  }
})
